import { defineStore } from 'pinia'
import api from '@/service'
const useHomeStore = defineStore('home', {
  state: () => ({
    hotSuggest: [],
    categories: [],
    hotlist: [],
    currentpage: 0
  }),
  actions: {
    async gitHotSuggest() {
      const res = await api.getHotCity()
      // console.log(res)
      this.hotSuggest = res.data
    },
    async gitCategories() {
      const res = await api.getCategories()
      // console.log(res)
      this.categories = res.data
    },
    async getHotList() {
      this.currentpage++
      const res = await api.getHotList(this.currentpage)
      // console.log(res)
      this.hotlist.push(...res.data)
    }
  }
})

export default useHomeStore
